import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import theme from './theme';
import AppBar from 'material-ui/AppBar';
import Drawer from 'material-ui/Drawer';
import MenuItem from 'material-ui/MenuItem';

export default class Main extends Component {

  constructor(props) {
    super(props);
    this.handleMenuClick = this.handleMenuClick.bind(this);
    this.state = { open: false };
    this.routeAndClose = this.routeAndClose.bind(this);
  }

  handleMenuClick() {
    this.setState({ open: !this.state.open });
  }

  routeAndClose(path) {
    this.props.router.push(path);
    this.setState({ open: false });
  }

  render() {
    const styles = {
      appBar: {
        position: 'fixed',
        top: 0
      }
    };

    return (
      <MuiThemeProvider muiTheme={theme}>
        <div className="wrapper">
          <AppBar title="Anti-D Decision Tree" onLeftIconButtonTouchTap={this.handleMenuClick} style={styles.appBar} />
          <div className="contents">
            { this.props.children }
          </div>
          <Drawer open={this.state.open} docked={false} onRequestChange={open => this.setState({open})}>
            <MenuItem onTouchTap={ () => { this.routeAndClose('/'); } }>Home</MenuItem>
            <MenuItem onTouchTap={ () => { this.routeAndClose('/questions'); } }>Questions</MenuItem>
            <MenuItem onTouchTap={ () => { this.routeAndClose('/about'); } }>About</MenuItem>
          </Drawer>
        </div>
      </MuiThemeProvider>
    )
  }
}



