import React from 'react';
import { Router, Route, IndexRoute, IndexRedirect, hashHistory, withRouter } from 'react-router'

import Main from './main';
import Home from './home';
import About from './about';
import Flow from './flow';


export default (
  <Router history={hashHistory}>
    <Route path="/" component={withRouter(Main)}>
      <IndexRoute component={Home} />
      <Route path="about" component={About} />
      <Route path="questions">
        <IndexRedirect to="6DE7DA1D-E226-4458-96FB-6C20A6B9AFE1" />
        <Route path=":id" component={withRouter(Flow)} />
      </Route>
    </Route>
  </Router>
);



