import React, { Component } from 'react';

import Paper from 'material-ui/Paper';
import RaisedButton from 'material-ui/RaisedButton';
import Checkbox from 'material-ui/Checkbox';

export default class Question extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    const style = {
      height: '100%',
      width: '100%',
      marginTop: 10,
      marginBottom: 10,
      padding: 10
    };

    const options = this.props.options && this.props.options.map(o => {
      return (
        <Paper key={o.id} style={style} zDepth={1}>
          <Checkbox
            label={o.description}
            checked={o.selected}
            onCheck={() => { this.props.onSelect(o); } } />
        </Paper>
      );
    });

    return (
      <div>
        { this.props.title &&
          <h2>{ this.props.title }</h2>
        }
        { this.props.description &&
          <div dangerouslySetInnerHTML={{ __html: this.props.description }} />
        }
        { options }
        { options &&
          <div className="question-actions">
            <RaisedButton
              label="Next"
              primary={true}
              disabled={!this.props.allowNext}
              onTouchTap={this.props.onNext} />
          </div>
        }
      </div>
    );
  }
}



