import React, { Component } from 'react';

import decisions from './decisions';
import Question from './question';

export default class Flow extends Component {

  constructor(props) {
    super(props);
    this.handleSelect = this.handleSelect.bind(this);
    this.handleNext = this.handleNext.bind(this);
    const question = decisions.filter(q => q.id === props.params.id)[0];
    this.state = {
      selectedQuestion: question || decisions[0],
      selectedOption: null
    };
    console.log('built flow');
  }

  handleSelect(option) {
    this.setState({ selectedOption: option });
  }

  handleNext() {
    // this.setState({
    //   selectedQuestion: this.state.selectedQuestion + 1,
    //   selectedOption: null
    // });

    this.props.router.push(`/questions/${this.state.selectedOption.next}`);
  }

  componentWillReceiveProps(nextProps) {
    const question = decisions.filter(q => q.id === nextProps.params.id)[0];
    this.setState({
      selectedQuestion: question || decisions[0],
      selectedOption: null
    });
  }

  render() {
    const options = this.state.selectedQuestion.options && this.state.selectedQuestion.options.map(o => {
      const option = Object.assign({}, o);
      option.selected = this.state.selectedOption && o.id === this.state.selectedOption.id;
      return option;
    });
    return (
      <Question
        title={this.state.selectedQuestion.title}
        description={this.state.selectedQuestion.description}
        options={options}
        onSelect={this.handleSelect}
        onNext={this.handleNext}
        allowNext={this.state.selectedOption && this.state.selectedOption.next} />
    );
  }
}



