import React, { Component } from 'react';
import Paper from 'material-ui/Paper';

export default class About extends Component {

  render() {
    return (
      <div>
        <p>About!</p>
        <Paper style={{ padding: 10 }}>
          <img src="images/nhs-logo.svg" alt="NHS Logo" style={{ width: '100%' }} />
        </Paper>
      </div>
    );
  }
}


